import React from "react";
import Machines from './components/assignedMachines'
import PatchingMachines from './components/patchingMachines';
import CompletedMachines from './components/completedMachines';

class machineWindow extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // dataUrl: 'http://192.168.1.30:3000',
      dataUrl: 'https://xtpprd01.cosmote.gr:3000',
      isLoaded: false,
      machines: [],
      machines_complete: [],
      selectedMachines: [],
      OSList: [],
      sliceList: [],
      bundleList:[],
      appSuppList:[],
      patchingOSList: [],
      patchingSliceList: [],
      patchingBundleList:[],
      patchingAppSuppList:[],
      completedOSList: [],
      completedSliceList: [],
      completedBundleList:[],
      completedAppSuppList:[],
      totalMachines: 0,
      availableMachines: 0,
      pendingMachines:0,
      patchingMachines: 0,
      completedMachines: 0,
      abortedMachines: 0
    };
  }

  componentDidMount() {

      const updateOSAndSliceList = () => {
        this.state.machines.forEach((e) => {
          if(!this.state.OSList.includes(e.TECHNOLOGY) && e.STATUS === null){
            this.state.OSList.push(e.TECHNOLOGY)
          }
          if(!this.state.sliceList.includes(e.SLICE_NAME) && e.STATUS === null){
            this.state.sliceList.push(e.SLICE_NAME)
          } 
          if(!this.state.bundleList.includes(e.PATCH_BUNDLE) && e.STATUS === null){
            this.state.bundleList.push(e.PATCH_BUNDLE)
          }
          if(!this.state.appSuppList.includes(e.AS_GROUP) && e.STATUS === null){
            this.state.appSuppList.push(e.AS_GROUP)
          }
        })
        this.state.machines_complete.forEach((e) => {
          if(!this.state.patchingOSList.includes(e.TECHNOLOGY) && e.STATUS === 'STARTED'){
            this.state.patchingOSList.push(e.TECHNOLOGY)
          }
          if(!this.state.patchingSliceList.includes(e.SLICE_NAME) && e.STATUS === 'STARTED'){
            this.state.patchingSliceList.push(e.SLICE_NAME)
          }
          if(!this.state.patchingBundleList.includes(e.PATCH_BUNDLE) && e.STATUS === 'STARTED'){
            this.state.patchingBundleList.push(e.PATCH_BUNDLE)
          }
          if(!this.state.patchingAppSuppList.includes(e.AS_GROUP) && e.STATUS === 'STARTED'){
            this.state.patchingAppSuppList.push(e.AS_GROUP)
          }
          if(!this.state.completedOSList.includes(e.TECHNOLOGY) && ((e.STATUS === 'COMPLETED') || (e.STATUS === 'ABORTED'))){
            this.state.completedOSList.push(e.TECHNOLOGY)
          }
          if(!this.state.completedSliceList.includes(e.SLICE_NAME) && ((e.STATUS === 'COMPLETED') || (e.STATUS === 'ABORTED'))){
            this.state.completedSliceList.push(e.SLICE_NAME)
          }
          if(!this.state.completedBundleList.includes(e.PATCH_BUNDLE) && ((e.STATUS === 'COMPLETED') || (e.STATUS === 'ABORTED'))){
            this.state.completedBundleList.push(e.PATCH_BUNDLE)
          }
          if(!this.state.completedAppSuppList.includes(e.AS_GROUP) && ((e.STATUS === 'COMPLETED') || (e.STATUS === 'ABORTED'))){
            this.state.completedAppSuppList.push(e.AS_GROUP)
          }
        })
        this.state.sliceList.sort()
        this.state.bundleList.sort()
        this.state.OSList.sort()
        this.state.appSuppList.sort()
        this.state.patchingOSList.sort()
        this.state.patchingSliceList.sort()
        this.state.patchingBundleList.sort()
        this.state.patchingAppSuppList.sort()
        this.state.completedOSList.sort()
        this.state.completedSliceList.sort()
        this.state.completedBundleList.sort()
        this.state.completedAppSuppList.sort()
      }

      const updateStatusList = () => {
        let pendingMachines=0, patchingMachines=0, completedMachines=0, abortedMachines=0
        this.state.machines.forEach((e)=>{
          if(e.STATUS === null){
            pendingMachines ++           
          } else if(e.STATUS === 'STARTED'){
            patchingMachines ++
          } else if(e.STATUS === 'COMPLETED'){
            completedMachines ++
          } else if(e.STATUS === 'ABORTED'){
            abortedMachines ++
          }
        })
        this.setState({
          pendingMachines:pendingMachines,
          patchingMachines: patchingMachines,
          completedMachines: completedMachines,
          abortedMachines: abortedMachines
        })
      }

      const updateInterval = () =>{
        fetch(this.state.dataUrl+'/api/machines/prod', {
        // fetch(this.state.dataUrl+'/api/machines/', {
          method: 'GET',
          mode: "cors",
          headers: {
            'content-type': 'application/json',
            'Accept': 'application/json'
                  }
        })
          .then(res => res.json())
          .then(
            (result) => {
              if(JSON.stringify(this.state.machines) !== JSON.stringify(result)){
                this.setState({
                  machines: result
                });
                updateOSAndSliceList()
                updateStatusList()
              }
            },
            (error) => {
              this.setState({
                error
              });
            }
          )

    }

    const updateIntervalComplete = () =>{
      fetch(this.state.dataUrl+'/api/machines/prod/complete', {
        // fetch(this.state.dataUrl+'/api/machines/complete', {
        method: 'GET',
        mode: "cors",
        headers: {
          'content-type': 'application/json',
          'Accept': 'application/json'
                }
      })
        .then(res => res.json())
        .then(
          (result) => {
            if(JSON.stringify(this.state.machines_complete) !== JSON.stringify(result)){
              this.setState({
                machines_complete: result
              });
              updateOSAndSliceList()
              updateStatusList()
            }
          },
          (error) => {
            this.setState({
              error
            });
          }
        )

  }

    const updateIntervalTotal = () =>{
      fetch(this.state.dataUrl+'/api/machines/total', {
        method: 'GET',
        mode: "cors",
        headers: {
          'content-type': 'application/json',
          'Accept': 'application/json'
                }
      })
        .then(res => res.json())
        .then(
          (result) => {
            if(this.state.totalMachines !== result[0].total){
              this.setState({
                totalMachines: result[0].total,
                availableMachines: result[1].toBePatched
              });
            }
          },
          (error) => {
            this.setState({
              error
            });
          }
        )

  }


    var machinesInt = setInterval(function() {
        updateInterval()
    }, 2000);

    var machinesInt = setInterval(function() {
      updateIntervalComplete()
  }, 2000);

    var totalInt = setInterval(function() {
      updateIntervalTotal()
  }, 10000);

  updateIntervalTotal()
  updateInterval()
  updateIntervalComplete()

  }


render(){

  return(
    <div className="row patchManager">
      <div className="col-1"></div>
      <div className="col-10">
        <div className="row text-light text-center mr-1 ml-1 mt-2 border border-dark rounded shadow bg-white">
          <div className="col-4 bg-secondary">Total: {this.state.availableMachines} / {this.state.totalMachines}</div>
          <div className="col-2 bg-info">Pending: {this.state.pendingMachines}</div>
          <div className="col-2 bg-warning">Patching: {this.state.patchingMachines}</div>
          <div className="col-2 bg-success">Completed: {this.state.completedMachines}</div>
          <div className="col-2 bg-danger">Aborted: {this.state.abortedMachines}</div>
        </div>
        <Machines dataUrl = {this.state.dataUrl} machines= {this.state.machines} OSList={this.state.OSList} sliceList={this.state.sliceList} bundleList={this.state.bundleList} appSuppList = {this.state.appSuppList} />
        <PatchingMachines dataUrl = {this.state.dataUrl} machines= {this.state.machines_complete} OSList={this.state.patchingOSList} sliceList={this.state.patchingSliceList} appSuppList = {this.state.patchingAppSuppList}/>
        <CompletedMachines dataUrl = {this.state.dataUrl} machines= {this.state.machines_complete} OSList={this.state.completedOSList} sliceList={this.state.completedSliceList} appSuppList = {this.state.completedAppSuppList}/>
      </div>
      <div className="col-1"></div>
    </div>    
  )
  } 

}

export default machineWindow


