import React from "react";
import RunningPipeline from './components/pipeline/runningPipeline'
import NotRunningPipeline from './components/pipeline/notRunningPipeline'


class machineWindow extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dataUrl: 'http://192.168.1.30:3000',
      // dataUrl: 'https://xtpprd01.cosmote.gr:3000',
      runningPipeline: [],
      notRunningPipeline: [],
    };
  }

  componentDidMount() {
    fetch(this.state.dataUrl+'/api/pipeline/running', {
      method: 'GET',
      mode: "cors",
      headers: {
        'content-type': 'application/json',
        'Accept': 'application/json'
              }
    })
      .then(res => res.json())
      .then(
        (result) => {
            this.setState({
              runningPipeline: result
            });
        },
        (error) => {
          this.setState({
            error
          });
        }
      )

      fetch(this.state.dataUrl+'/api/pipeline/notrunning', {
        method: 'GET',
        mode: "cors",
        headers: {
          'content-type': 'application/json',
          'Accept': 'application/json'
                }
      })
        .then(res => res.json())
        .then(
          (result) => {
              this.setState({
                notRunningPipeline: result
              });
          },
          (error) => {
            this.setState({
              error
            });
          }
        )

  }
render(){
  return(
    <div className="row patchManager">
    <div className="col-1"></div>
    <div className="col-10">
      <RunningPipeline machines = {this.state.runningPipeline}/>
      <NotRunningPipeline machines = {this.state.notRunningPipeline}/>
    </div>
    <div className="col-1"></div>
  </div> 
  )
}
}

export default machineWindow


