import React from "react";
import Select from 'react-select'
import Cookies from 'js-cookie';

class patchingMachines extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dataUrl: '',
            // dataUrl: 'https://xtpprd01.cosmote.gr:3000',
            machines: [],
            filtered: 0,
            patchingMachines: [],
            actionCount: 0,
            oldValue :'',
            OSList: [],
            sliceList: [],
            appSuppList: [],
            selectedSliceList: [],
            selectedOSList: [],
            selectedAppSuppList: []
        };
      }
    
      static getDerivedStateFromProps(nextProps, prevState) {        
        let OSList = []
        nextProps.OSList.forEach((e)=>{
        OSList.push({value: e, label: e})
        })

        let sliceList = []
        nextProps.sliceList.forEach((e)=>{
          sliceList.push({value: e, label: e})
        })
        let appSuppList = []
        nextProps.appSuppList.forEach((e)=>{
          appSuppList.push({value: e, label: e})
        })
        
        return {
            machines: nextProps.machines,
            patchingMachines: nextProps.patchingMachines,
            OSList: OSList,
            sliceList: sliceList,
            dataUrl:nextProps.dataUrl,
            appSuppList:appSuppList,
        };
       }
       
       render() {
    
        const postData = () =>{
          let patchList = []
          let requestList =[]
          let printList = []
          this.state.machines.forEach((e) => {
            if(e.STATUS ==='STARTED'){
              patchList.push(e.HOST_NAME)
            }
          })
          let username = Cookies.get('username').split('@')[0]
          // let username = 'gmatentz33' 
          patchList.forEach((e)=>{
            let option = document.getElementById('patchingList'+e).value
            if(option === 'COMPLETED' || option === 'ABORTED'){
            requestList.push({'HOST_NAME':e,
                                'STATUS':option,
                              'USERNAME':username})
                                printList.push(e+'('+option+')')          
            }
          })
          var r = window.confirm("You are gonna perform action(s) to: " + printList)
          if(r){
          fetch(this.state.dataUrl+'/api/update/prod', {
            method : 'POST',
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              patchList: requestList
            })
          })
          }
        }

        function sortTable(n) {
          var table, rows, switching, i, x, y, shouldSwitch, dir, switchcount = 0;
          table = document.getElementById("patchingTable");
          switching = true;
          dir = "asc";
          while (switching) {
            switching = false;
            rows = table.rows;
            for (i = 1; i < (rows.length - 1); i++) {
              shouldSwitch = false;
              x = rows[i].getElementsByTagName("TD")[n];
              y = rows[i + 1].getElementsByTagName("TD")[n];
              if (dir === "asc") {
                if (x.innerHTML.toLowerCase() > y.innerHTML.toLowerCase()) {
                  shouldSwitch = true;
                  break;
                }
              } else if (dir === "desc") {
                if (x.innerHTML.toLowerCase() < y.innerHTML.toLowerCase()) {
                  shouldSwitch = true;
                  break;
                }
              }
            }
            if (shouldSwitch) {
              rows[i].parentNode.insertBefore(rows[i + 1], rows[i]);
              switching = true;
              switchcount ++;
            } else {
              if (switchcount === 0 && dir === "asc") {
                dir = "desc";
                switching = true;
              }
            }
          }
        }

        const formatedDate = (date) => {
          return(date.getDate()+'/'+parseInt(date.getMonth()+1)+'/'+date.getFullYear().toString().substr(2,2)+' '+(date.getHours()<10?'0'+date.getHours():date.getHours())+':'+(date.getMinutes()<10?'0'+date.getMinutes():date.getMinutes()))
        }
        
        const changeFilter = (name, e) => {
          console.log(name, e)
          let tmpList = []
          switch(name){
            case 'selectedSliceOption':
              e.forEach(element => {
                tmpList.push(element.value)
              })
              this.setState({
                selectedSliceList: tmpList
              }, () => {
                searchFunction()
              })
              break
            case 'selectedOSOption':
              e.forEach(element => {
                tmpList.push(element.value)
              })
              this.setState({
                selectedOSList: tmpList
              }, () => {
                searchFunction()
              })
              break
            case 'selectedAppSuppOption':
              e.forEach(element => {
                tmpList.push(element.value)
              })
              this.setState({
                selectedAppSuppList: tmpList
              }, () => {
                searchFunction()
              })
              break
          }
          
        }

        const searchFunction = (e) => {
          let input, filter, table, tr, td, i, txtValue, td1, txtValue1, td2, txtValue2, td3, txtValue3
          input = document.getElementById("searchPatchingInput");
          filter = input.value.toUpperCase();
          table = document.getElementById("patchingTable");
          tr = table.getElementsByTagName("tr");
    
          let filtered = 0

        
          // Loop through all table rows, and hide those who don't match the search query
          for (i = 0; i < tr.length; i++) {
            td = tr[i].getElementsByTagName("td")[0];
            td1 = tr[i].getElementsByTagName("td")[1];
            td2 = tr[i].getElementsByTagName("td")[2];
            td3 = tr[i].getElementsByTagName("td")[3];
            
            if (td) {
              txtValue = td.textContent || td.innerText;
              txtValue1 = td1.textContent || td1.innerText;
              txtValue2 = td2.textContent || td2.innerText;
              txtValue3 = td3.textContent || td3.innerText;

              if ((txtValue.toUpperCase().indexOf(filter) > -1 &&
              (this.state.selectedSliceList.includes(txtValue1) || this.state.selectedSliceList.length === 0) &&
              (this.state.selectedOSList.includes(txtValue2) || this.state.selectedOSList.length === 0) &&
              (this.state.selectedAppSuppList.includes(txtValue3) || this.state.selectedAppSuppList.length === 0)
              
              )){
                tr[i].style.display = "";
                filtered ++

              } else {
                tr[i].style.display = "none";
              }
            }
          }
          if(this.state.filtered !== filtered){
          this.setState({
            filtered:filtered
          })}
        }

        if(document.getElementById("searchPatchingInput")){
          searchFunction()
        }

        const countUpdate= (e) =>{
          if(e.target.value === 'PATCHING'){
            this.setState({actionCount: this.state.actionCount -1 })
          } else if(this.state.oldValue === 'PATCHING'){
            this.setState({actionCount: this.state.actionCount +1 })
            }
          
          if(e.target.value === 'PATCHING'){
            this.setState({oldValue: 'PATCHING'})
          } else{
            this.setState({oldValue: ''})
          }  
          }


        const previusValue = (e) =>{
          this.setState({oldValue: e.target.value})
        }

        const { machines } = this.state;
          return (
            <div className='mt-4 p-3 border border-warning rounded shadow bg-white'>
            <div className='row pt-2'><div className='col-12'>
              <h4>below servers are under patching</h4>
              <div className="table-responsive tableMaxHeight tableFixHead">
                <table className='table table-sm' id='patchingTable'>
                  <thead>
                    <tr>
                      <th className='sortable'> <span onClick={(e) => sortTable(0)}>HOST NAME </span><input type="text" id="searchPatchingInput" onKeyUp={((e) => searchFunction(e))} placeholder="Search Hosts..."></input></th>
                      <th className='sortable'><span onClick={(e) => sortTable(1)}>SLICE NAME:</span>
                        <Select 
                          name="SliceOption"
                          menuPortalTarget={document.body} //appear on top
                          styles={{ menuPortal: base => ({ ...base, zIndex: 9999 }) }} //appear on top
                          isMulti 
                          options={this.state.sliceList}
                          onChange={(e) => changeFilter('selectedSliceOption', e)} 
                          className="basic-multi-select form-select"
                          classNamePrefix="select"
                        />
                      </th>
                      <th className='sortable'><span onClick={(e) => sortTable(2)}>OS:</span>
                        <Select 
                          name="OSOption"
                          menuPortalTarget={document.body} //appear on top
                          styles={{ menuPortal: base => ({ ...base, zIndex: 9999 }) }} //appear on top
                          isMulti 
                          options={this.state.OSList}
                          onChange={(e) => changeFilter('selectedOSOption', e)} 
                          className="basic-multi-select form-select"
                          classNamePrefix="select"
                        />
                      </th>
                      <th className='sortable'><span onClick={(e) => sortTable(3)}>APPLICATION SUPPORT GROUP:</span>
                        <Select 
                          name="AppSuppOption"
                          menuPortalTarget={document.body} //appear on top
                          styles={{ menuPortal: base => ({ ...base, zIndex: 9999 }) }} //appear on top
                          isMulti 
                          options={this.state.appSuppList}
                          onChange={(e) => changeFilter('selectedAppSuppOption', e)} 
                          className="basic-multi-select form-select"
                          classNamePrefix="select"
                        />
                      </th>
                      <th className='sortable' onClick={(e) => sortTable(4)}>STARTED TIME</th>
                      <th className='sortable' onClick={(e) => sortTable(5)}>STARTED BY</th>
                      <th>ACTION</th>
                    </tr>
                  </thead>
                  <tbody>
                    {machines.map(item => {return item.STATUS === 'STARTED'?(
                      <tr key={item.HOST_NAME} id= {'tr'+item.HOST_NAME}>
                        <td>{item.HOST_NAME}</td><td>{item.SLICE_NAME}</td><td>{item.TECHNOLOGY}</td><td>{item.AS_GROUP}</td>
                        <td>{formatedDate(new Date(item.STARTED_TIME))}</td><td>{item.USERNAME}</td><td>
                        <label htmlFor={"patchingList"+ item.HOST_NAME}>Choose an action:</label>
                          <select id={"patchingList"+ item.HOST_NAME } onChange = {(e) => countUpdate(e)} onFocus = {(e) => previusValue(e)}>
                            <option value="PATCHING">---</option>
                            <option value="COMPLETED">Completed</option>
                            <option value="ABORTED">Failed (ABORTED)</option>
                            <option value="ABORTED">Canceled (ABORTED)</option>
                          </select>
                        </td>
                      </tr>
                    ):
                    null
                    })}
                  </tbody>
                </table>
              </div>
          <div>Total: {this.state.filtered}</div>
          <div>To do an Action: {this.state.actionCount}</div>
          
            </div></div>
            <div className='row mt-1'><div className='col-12'>
              <div className='btn-group'>
                <button className='btn btn-primary' onClick={(e) => postData(e)}>action</button>
              </div>
            </div></div>
            </div>
          )
      }
    }

export default patchingMachines;


