import React from "react";
import Select from 'react-select'
import Cookies from 'js-cookie';

class assignedMachines extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        dataUrl: '',
        error: null,
        firstLoad: true,
        machines: [],
        filtered: 0,
        selectedMachines: [],
        readyForPatchList: [],
        patchingMachines: [],
        sliceList: [],
        OSList: [],
        bundleList:[],
        appSuppList:[],
        selectedSliceList: [],
        selectedOSList: [],
        selectedBundleList:[],
        selectedAppSuppList:[]
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    let readyForPatch = []
    nextProps.machines.forEach((e)=>{
      if(e.READY_FOR_PATCH === 2){
        readyForPatch.push(e.HOST_NAME)
      }
      
    })
    let OSList = []
    nextProps.OSList.forEach((e)=>{
      OSList.push({value: e, label: e})
    })
    let sliceList = []
    nextProps.sliceList.forEach((e)=>{
      sliceList.push({value: e, label: e})
    })
    let bundleList = []
    nextProps.bundleList.forEach((e)=>{
      bundleList.push({value: e, label: e})
    })
    let appSuppList = []
    nextProps.appSuppList.forEach((e)=>{
      appSuppList.push({value: e, label: e})
    })
    return {
        machines: nextProps.machines,
        patchingMachines: nextProps.patchingMachines,
        OSList: OSList,
        sliceList: sliceList,
        bundleList: bundleList,
        dataUrl:nextProps.dataUrl,
        appSuppList:appSuppList,
        readyForPatchList:readyForPatch
    };
   }
       
   render() {
    const selectHandler = (e, id) => {
      let tr = document.getElementById('tr'+id.HOST_NAME)
      if(this.state.selectedMachines.includes(id.HOST_NAME)){
        const index = this.state.selectedMachines.indexOf(id.HOST_NAME);
        if (index > -1) {
          let tmpSelectedArray = this.state.selectedMachines
          tmpSelectedArray.splice(index, 1);
          // this.state.selectedMachines.splice(index, 1);
          this.setState({
            selectedMachines: tmpSelectedArray
          })
        }
        tr.style.backgroundColor=''
      } else if(!this.state.readyForPatchList.includes(id.HOST_NAME)){
        return null
      } else{
        let tmpSelectedArray = this.state.selectedMachines
        tmpSelectedArray.push(id.HOST_NAME)
        // this.state.selectedMachines.push(id.HOST_NAME)
        tr.style.backgroundColor='lightgray'
        this.setState({
          selectedMachines: tmpSelectedArray
        })
      }
    }

    const postData = () =>{
      let patchList = []
      this.state.machines.forEach((e)=>{
        if(this.state.selectedMachines.includes(e.HOST_NAME)&&e.READY_FOR_PATCH===2){
          let username = Cookies.get('username').split('@')[0]
          // let username = 'gmatentz' 
          patchList.push({'HOST_NAME':e.HOST_NAME,
                          'STATUS':'STARTED',
                          'USERNAME': username
                          })
        }
      })
      fetch(this.state.dataUrl+'/api/update/prod', {
        method : 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          patchList: patchList
      })
    })
    }

    const clearBgr = () => {
      this.state.selectedMachines.forEach(e => { //clear background color
      let tr = document.getElementById('tr'+e)
      tr.style.backgroundColor=''
      })
      this.setState({
        selectedMachines: []
      })
    }

    const updateHandler = (e) =>{
      var r = window.confirm("Do you wanna patch: "+ this.state.selectedMachines)
      if(r){
        postData()
        clearBgr()
        this.setState({selectedMachines:[]}) //clear patch list
        
      }else{alert('Canceled!')}
    }

    const changeFilter = (name, e) => {
      console.log(name, e)
      let tmpList = []
      switch(name){
        case 'selectedSliceOption':
          e.forEach(element => {
            tmpList.push(element.value)
          })
          this.setState({
            selectedSliceList: tmpList
          }, () => {
            searchHost()
          })
          break
        case 'selectedOSOption':
          e.forEach(element => {
            tmpList.push(element.value)
          })
          this.setState({
            selectedOSList: tmpList
          }, () => {
            searchHost()
          })
          break
        case 'selectedBundleOption':
          e.forEach(element => {
            tmpList.push(element.value)
          })
          this.setState({
            selectedBundleList: tmpList
          }, () => {
            searchHost()
          })
          break
        case 'selectedAppSuppOption':
          e.forEach(element => {
            tmpList.push(element.value)
          })
          this.setState({
            selectedAppSuppList: tmpList
          }, () => {
            searchHost()
          })
          break
      }
      
    }

    const searchHost = (e) => {
        let input, filter, table, tr, td, i, txtValue, td1, txtValue1, td2, txtValue2, td3, txtValue3, td4, txtValue4

        input = document.getElementById("searchInput");
        filter = input.value.toUpperCase();
        table = document.getElementById("machinesTable");
        tr = table.getElementsByTagName("tr");

        let filtered = 0
      
        // Loop through all table rows, and hide those who don't match the search query
        for (i = 0; i < tr.length; i++) {
          td = tr[i].getElementsByTagName("td")[0];
          td1 = tr[i].getElementsByTagName("td")[1];
          td2 = tr[i].getElementsByTagName("td")[2];
          td3 = tr[i].getElementsByTagName("td")[3];
          td4 = tr[i].getElementsByTagName("td")[4];

          if (td) {
            txtValue = td.textContent || td.innerText;
            txtValue1 = td1.textContent || td1.innerText;
            txtValue2 = td2.textContent || td2.innerText;
            txtValue3 = td3.textContent || td3.innerText;
            txtValue4 = td4.textContent || td4.innerText;


            if ((txtValue.toUpperCase().indexOf(filter) > -1 &&
            (this.state.selectedSliceList.includes(txtValue1) || this.state.selectedSliceList.length === 0) &&
            (this.state.selectedOSList.includes(txtValue2) || this.state.selectedOSList.length === 0) &&
            (this.state.selectedBundleList.includes(txtValue3) || this.state.selectedBundleList.length === 0) &&
            (this.state.selectedAppSuppList.includes(txtValue4) || this.state.selectedAppSuppList.length === 0)
            )){
              tr[i].style.display = "";
              filtered ++
            } else {
              tr[i].style.display = "none";
              
            }
          }
        }
        if(this.state.filtered !== filtered){
          this.setState({
            filtered:filtered
          })}
    }
    function sortTable(n) {
      var table, rows, switching, i, x, y, shouldSwitch, dir, switchcount = 0;
      table = document.getElementById("machinesTable");
      switching = true;
      dir = "asc";
      while (switching) {
        switching = false;
        rows = table.rows;
        for (i = 1; i < (rows.length - 1); i++) {
          shouldSwitch = false;
          x = rows[i].getElementsByTagName("TD")[n];
          y = rows[i + 1].getElementsByTagName("TD")[n];
          if (dir === "asc") {
            if (x.innerHTML.toLowerCase() > y.innerHTML.toLowerCase()) {
              shouldSwitch = true;
              break;
            }
          } else if (dir === "desc") {
            if (x.innerHTML.toLowerCase() < y.innerHTML.toLowerCase()) {
              shouldSwitch = true;
              break;
            }
          }
        }
        if (shouldSwitch) {
          rows[i].parentNode.insertBefore(rows[i + 1], rows[i]);
          switching = true;
          switchcount ++;
        } else {
          if (switchcount === 0 && dir === "asc") {
            dir = "desc";
            switching = true;
          }
        }
      }
    }

    const formatedDate = (date) => {
      return(date.getDate()+'/'+parseInt(date.getMonth()+1)+'/'+date.getFullYear().toString().substr(2,2))
    }

    const { error, machines } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else {
      return (
        <div className='mt-4 p-3 border border-secondary rounded shadow bg-white'>
        <div className='row pt-2'><div className='col-12'>
          <h4>below servers are ready for patching</h4>
          <p>select one or more and click "patch" to begin the proccess</p>
          <p>patch cycle: {(machines[0])?(machines[0].PATCH_CYCLE):('?')}</p>
          <div className="table-responsive tableMaxHeight tableFixHead">
          <table className='table table-sm table-hover' id='machinesTable'>
            <thead>
              <tr>
                <th> <span className='sortable' onClick={(e) => sortTable(0)}>HOST NAME<br/></span>
                  <input type="text" id="searchInput" onKeyUp={((e) => searchHost(e))} placeholder="Search Hosts..."></input>
                </th>
                <th> <span className='sortable' onClick={(e) => sortTable(1)} >SLICE NAME:</span>
                  <Select 
                      name="SliceOption"
                      menuPortalTarget={document.body} //appear on top
                      styles={{ menuPortal: base => ({ ...base, zIndex: 9999 }) }} //appear on top
                      isMulti 
                      options={this.state.sliceList}
                      onChange={(e) => changeFilter('selectedSliceOption', e)} 
                      className="basic-multi-select form-select"
                      classNamePrefix="select"
                     />
                    </th>
                  <th><span className='sortable' onClick={(e) => sortTable(2)}>OS:</span>
                <Select 
                      name="OSOption"
                      menuPortalTarget={document.body}  //appear on top
                      styles={{ menuPortal: base => ({ ...base, zIndex: 9999 }) }} //appear on top
                      isMulti 
                      options={this.state.OSList} 
                      onChange={(e) => changeFilter('selectedOSOption',e)} 
                      className="basic-multi-select"
                      classNamePrefix="select"
                     />
                  </th>
                  <th><span className='sortable' onClick={(e) => sortTable(3)}>PATCH BUNDLE:</span>
                    <Select 
                      name="bundleOption"
                      menuPortalTarget={document.body}  //appear on top
                      styles={{ menuPortal: base => ({ ...base, zIndex: 9999 }) }} //appear on top
                      isMulti 
                      options={this.state.bundleList} 
                      onChange={(e) => changeFilter('selectedBundleOption', e)} 
                      className="basic-multi-select"
                      classNamePrefix="select"
                     />
                  </th>
                  <th className='sortable'><span onClick={(e) => sortTable(4)}>
                  APPLICATION SUPPORT GROUP:</span>
                     <Select 
                      name="appSuppOption"
                      menuPortalTarget={document.body} //appear on top
                      styles={{ menuPortal: base => ({ ...base, zIndex: 9999 }) }} //appear on top
                      isMulti 
                      options={this.state.appSuppList} 
                      onChange={(e)=> changeFilter('selectedAppSuppOption', e)} 
                      className="basic-multi-select"
                      classNamePrefix="select"
                     />
                  </th>
                  <th className='sortable'><span onClick={(e) => sortTable(5)}>Patch Date</span></th>
                  <th className='sortable'><span onClick={(e) => sortTable(6)}>Ready for Patch</span>
                  </th>
              </tr>
            </thead>
            <tbody>
              {machines.map(item => {
                return item.STATUS === null &&(item.READY_FOR_PATCH===2 || !this.state.readyForPatchList.includes(item.HOST_NAME) )? 
                <tr key={item.HOST_NAME} id= {'tr'+item.HOST_NAME} title = {item.ENDED_TIME?'ended: '+new Date(item.ENDED_TIME).toString():''}onClick={(e) => selectHandler(e, item)} style={item.READY_FOR_PATCH===1?{'backgroundColor':'DarkSeaGreen'}:item.STATUS==='COMPLETED'?{'backgroundColor':'LIGHTGREEN'}:item.STATUS==='ABORTED'?{'backgroundColor':'RED'}:null}>
                  <td>{item.HOST_NAME}</td><td>{item.SLICE_NAME}</td><td>{item.TECHNOLOGY}</td><td>{item.PATCH_BUNDLE}</td><td>{item.AS_GROUP}</td>
                  <td>{formatedDate(new Date(item.SCHEDULED_DATE))}</td>
                  <td>{item.READY_FOR_PATCH===1?'NO':'YES'}</td>
                </tr>:null
              })}
            </tbody>
          </table>
          </div>
          <div>Total: {this.state.filtered}</div>
          <div>Selected: {this.state.selectedMachines.length}</div>
        </div></div>
        <div className='row mt-1'><div className='col-12'>
          <div className='btn-group'>
            <button className='btn btn-primary' onClick={(e) => updateHandler(e)}>Patch</button>
            <button className='btn btn-danger' onClick={(e) => clearBgr()}>clear selection</button>
          </div>
        </div></div>
        </div>
      )
    }
  }
}

export default assignedMachines;


