import React from "react";

class notRunningPipeline extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            machines:[]
        }
    }

    static getDerivedStateFromProps(nextProps, prevState) {    
      
        return {
            machines: nextProps.machines
        }
    
    }

    render(){

        const formatedDate = (date) => {
            return(date.getDate()+'/'+date.getMonth()+'/'+date.getFullYear().toString().substr(2,2)+' '+(date.getHours()<10?'0'+date.getHours():date.getHours())+':'+(date.getMinutes()<10?'0'+date.getMinutes():date.getMinutes()))
        }

        return(
            <div className='mt-4 p-3 border border-secondary rounded shadow bg-white'>
                <h4>Not Running pipeline:</h4>
                <div className="table-responsive tableMaxHeight tableFixHead">
                    <table className='table table-sm table-hover' id='machinesTable'>
                        <thead>
                            <tr>
                                <th>SLICE NAME</th><th>PIPELINE ID</th><th>PIPELINE STATUS</th><th>INSERT DATE</th>
                                <th>EXCEL_NAME</th><th>STEP EXCEL ID</th><th>STEP RUN ID</th><th>STEP STATUS ID</th>
                                <th>STEP OPERATION</th><th>STEP COMMENT</th><th>STEP EXEC</th><th>STEP TARGET SERVER</th>
                                <th>STEP START TIME</th><th>STEP END TIME</th><th>EXECUTOR</th><th>PATCH CYCLE</th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.machines.map((item)=>{return(
                                <tr key={item[0]+item[1]+item[2]+item[3]+item[4]+item[5]+item[6]+item[7]+item[8]+item[9]+item[10]+item[11]+item[12]+item[13]+item[14]}>
                                    <td>{item[0]}</td>
                                    <td>{item[1]}</td>
                                    <td>{item[2]}</td>
                                    <td>{formatedDate(new Date(item[3]))}</td>
                                    <td>{item[4]}</td>
                                    <td>{item[5]}</td>
                                    <td>{item[6]}</td>
                                    <td>{item[7]}</td>
                                    <td>{item[8]}</td>
                                    <td>{item[9]}</td>
                                    <td>{item[10]}</td>
                                    <td>{item[11]}</td>
                                    <td>{formatedDate(new Date(item[12]))}</td>
                                    <td>{formatedDate(new Date(item[13]))}</td>
                                    <td>{item[14]}</td>
                                    <td>{item[15]}</td>
                                </tr>
                            )})}
                        </tbody>
                    </table>
                </div>
            </div>    
        )
    }

}

export default notRunningPipeline


